.card-babysitters {
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    background: #FFFFFF;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    transition: box-shadow .3s ease;
    cursor: pointer;
    &:hover {
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
    }
    &__overlay-tags {
        position: absolute;
        top: 0;
        left: 0;
        padding: 16px 11px;
    }
    &__overlay-tag {
        background: rgba(#fff, .9);
        border-radius: 10px;
        padding: 5px 10px;
        font-size: 12px;
        line-height: 24px;
        color: #000;
    }
    &__body {
        padding: 18px 14px;
    }
    &__name {
        margin-right: 7px;
        font-family: $reg;
        font-weight: bold;
        font-size: 20px;
        line-height: 32px;
        color: #373737;
    }
    &__age {
        font-family: $reg;
        font-size: 20px;
        line-height: 32px;
        color: $graphite;
    }
    &__tags {
        margin-top: 10px;
        display: flex;
        .profile-tag {
            margin-right: 10px;
            &:last-child {
                margin-right: 0;
            }
        }
    }
}