@font-face {
  font-family: 'swiper-icons';
  src: url("data:application/font-woff;charset=utf-8;base64, ") format("woff");
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #007aff;
}

.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-container-pointer-events {
  touch-action: pan-y;
}

.swiper-container-pointer-events.swiper-container-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */

.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* 3D Effects */

.swiper-container-3d {
  perspective: 1200px;
}

.swiper-container-3d .swiper-wrapper,
.swiper-container-3d .swiper-slide,
.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom,
.swiper-container-3d .swiper-cube-shadow {
  transform-style: preserve-3d;
}

.swiper-container-3d .swiper-slide-shadow-left,
.swiper-container-3d .swiper-slide-shadow-right,
.swiper-container-3d .swiper-slide-shadow-top,
.swiper-container-3d .swiper-slide-shadow-bottom {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 10;
}

.swiper-container-3d .swiper-slide-shadow-left {
  background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-right {
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-top {
  background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

.swiper-container-3d .swiper-slide-shadow-bottom {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
}

/* CSS Mode */

.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

@font-face {
  font-weight: 700;
  font-family: "Raleway";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Raleway/Raleway-Bold.eot");
  src: url("../fonts/Raleway/Raleway-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Raleway/Raleway-Bold.woff2") format("woff2"), url("../fonts/Raleway/Raleway-Bold.woff") format("woff"), url("../fonts/Raleway/Raleway-Bold.svg#../fonts/Raleway/Raleway-Bold") format("svg");
}

@font-face {
  font-weight: 800;
  font-family: "Raleway";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Raleway/Raleway-ExtraBold.eot");
  src: url("../fonts/Raleway/Raleway-ExtraBold.eot?#iefix") format("embedded-opentype"), url("../fonts/Raleway/Raleway-ExtraBold.woff2") format("woff2"), url("../fonts/Raleway/Raleway-ExtraBold.woff") format("woff"), url("../fonts/Raleway/Raleway-ExtraBold.svg#../fonts/Raleway/Raleway-ExtraBold") format("svg");
}

* {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  font-weight: 500;
  font-family: "Inter", sans-serif;
  --safe-area-inset-top: env(safe-area-inset-top);
  --safe-area-inset-bottom: env(safe-area-inset-bottom);
  --safe-area-inset-left: env(safe-area-inset-left);
  --safe-area-inset-right: env(safe-area-inset-right);
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
li {
  margin: 0;
  padding: 0;
}

ul,
li,
ol {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

main.page {
  padding-top: 82px;
  padding-bottom: 106px;
}

.pb-0 {
  padding-bottom: 0 !important;
}

h1,
.page-title {
  margin-bottom: 20px;
  font-family: "Raleway", sans-serif;
  color: #3C3C3C;
  font-weight: 800;
  font-size: 52px;
  line-height: 1.17;
  letter-spacing: -0.7px;
}

.section-title {
  color: #0081C8;
  font-family: "Inter", sans-serif;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.3;
  text-align: center;
}

h2 {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 38px;
  line-height: 45px;
  color: #3C3C3C;
}

a {
  text-decoration: none;
  transition: all .3s ease;
}

a:hover {
  color: #36BCBB;
}

.tac {
  text-align: center;
}

.bg-navy {
  background-color: #0081C8;
}

.text-navy {
  color: #0081C8;
}

.text-white,
.text-white * {
  color: #ffffff;
}

.text-gray {
  color: #AAAAAA;
}

.bold {
  font-weight: 700;
}

.bg-light {
  background-color: #F3F4F6;
}

.bg-light-gray {
  background-color: #F7F7F7;
}

.section {
  padding: 60px 0;
}

.text-26 {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 26px;
  line-height: 1.3;
  text-align: center;
  color: #3C3C3C;
}

.container {
  max-width: 1170px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.container-video {
  max-width: 980px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -16px;
}

.col-4 {
  width: calc(100% / 12 * 4 - 32px);
  margin: 0 16px;
}

.btn {
  display: inline-block;
  background: #36BCBB;
  border: 1px solid #36BCBB;
  border-radius: 30px;
  padding: 20px 30px;
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  transition: all .3s ease;
  cursor: pointer;
}

.btn:hover {
  background-color: #ffffff;
  color: #36BCBB;
}

.btn-blue {
  color: #0081C8;
  background-color: #fff;
  border-color: #fff;
}

.btn-blue:hover {
  background-color: transparent;
  color: #ffffff;
}

.btn-o {
  font-weight: 500;
  color: #3C3C3C;
  background-color: transparent;
}

.btn-o:hover {
  background-color: #36BCBB;
  color: #ffffff;
}

.btn-white-o {
  font-weight: 500;
  color: #ffffff;
  background-color: transparent;
  border-color: #ffffff;
}

.btn-white-o:hover {
  background-color: #ffffff;
  color: #3C3C3C;
}

.link-arrow {
  position: relative;
  padding-right: 40px;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 1.22;
  color: #6D6D6D;
  text-decoration: none;
}

.link-arrow:after {
  position: absolute;
  display: inline-block;
  content: '';
  top: 3px;
  right: 0;
  width: 24px;
  height: 15px;
  transition: all .3s ease;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='15' viewBox='0 0 24 15' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.7071 8.20711C24.0976 7.81658 24.0976 7.18342 23.7071 6.7929L17.3431 0.428934C16.9526 0.0384093 16.3195 0.0384092 15.9289 0.428934C15.5384 0.819458 15.5384 1.45262 15.9289 1.84315L21.5858 7.5L15.9289 13.1569C15.5384 13.5474 15.5384 14.1805 15.9289 14.5711C16.3195 14.9616 16.9526 14.9616 17.3431 14.5711L23.7071 8.20711ZM-8.74228e-08 8.5L23 8.5L23 6.5L8.74228e-08 6.5L-8.74228e-08 8.5Z' fill='%2336BCBB'/%3E%3C/svg%3E%0A");
}

.link-arrow:hover::after {
  right: -3px;
}

.link {
  position: relative;
  font-family: "Inter", sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #3C3C3C;
  text-decoration: none;
}

.link:after {
  position: absolute;
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: #36BCBB;
  transition: all .3s ease;
}

.link:hover::after {
  bottom: -7px;
}

.swiper-pagination {
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  border: 1px solid #797979;
  border-radius: 50%;
  margin: 0 16px;
}

.swiper-pagination-bullet-active {
  width: 14px;
  height: 14px;
  background: #E8ACB9;
  border-color: #E8ACB9;
}

.socials {
  display: flex;
  align-items: center;
  margin: 0 -8px;
}

.socials__icon {
  display: block;
  margin-left: 8px;
}

