  max-width: 980px;
  margin: auto;
  padding-right: 15px;
  padding-left: 15px;
}

.features-services-title {
  margin-bottom: 69px;
}

.features-services-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 60px;
}

.features-services__string {
  display: flex;
  flex-basis: 47%;
  align-items: flex-start;
  margin-bottom: 26px;
}

.features-services__img {
  width: 24px;
  height: 25px;
  margin-top: 5px;
  margin-right: 18px;
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.0993 23.8967C8.3598 23.8967 7.6594 23.5516 7.2106 22.9566L1.1365 14.9309C0.948706 14.683 0.811605 14.4006 0.733033 14.0997C0.654462 13.7988 0.635962 13.4854 0.67859 13.1774C0.721219 12.8693 0.824139 12.5727 0.981469 12.3045C1.1388 12.0363 1.34745 11.8017 1.5955 11.6142C1.84346 11.4258 2.12615 11.2883 2.42736 11.2094C2.72857 11.1306 3.04238 11.1119 3.35082 11.1546C3.65925 11.1972 3.95623 11.3003 4.22475 11.4579C4.49326 11.6156 4.72803 11.8246 4.9156 12.0732L8.9123 17.35L18.961 1.21356C19.2943 0.680776 19.8252 0.301926 20.4374 0.160107C21.0497 0.0182878 21.6931 0.125079 22.2267 0.457057C23.3368 1.14726 23.6785 2.60926 22.9849 3.72106L11.1121 22.7781C10.9094 23.1048 10.6301 23.3772 10.2983 23.5715C9.96646 23.7659 9.59225 23.8763 9.2081 23.8933C9.1707 23.8967 9.1367 23.8967 9.0993 23.8967Z' fill='%2336BCBB'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
}

.features-services__text {
  width: 410px;
  font-weight: normal;
  font-size: 18px;
  line-height: 22px;
  font-family: "Inter", sans-serif;
  color: #000000;
}

.features-services-cost {
  margin-bottom: 18px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  font-family: "Inter", sans-serif;
  text-align: center;
  color: #6D6D6D;
}

.features-services-btn {
  text-align: center;
}

.albums {
  padding-top: 83px;
  padding-bottom: 74px;
  background-color: #F7F7F7;
}

.albums-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.albums-descr {
  flex-basis: 55%;
  margin-right: 52px;
}

.albums-title {
  margin-bottom: 40px;
  font-weight: 800;
  font-size: 52px;
  line-height: 61px;
  font-family: "Raleway", sans-serif;
  color: #3C3C3C;
}

.albums-preview {
  flex-basis: 45%;
}

.albums-list {
  margin-left: 18px;
}

.albums-list__item {
  width: 500px;
  margin-bottom: 31px;
  list-style: disc;
}

.albums-list__item:last-child {
  margin-bottom: 0;
}

.albums-list__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  font-family: "Inter", sans-serif;
  color: #3C3C3C;
}

.pricelist {
  padding-top: 48px;
  padding-bottom: 80px;
  background-color: #ffffff;
}

.pricelist-container {
  width: 100%;
  max-width: 1175px;
  margin: auto;
  padding-right: 15px;
  padding-left: 15px;
}

.pricelist-title {
  margin-bottom: 40px;
}

.pricelist-table {
  width: 100%;
  margin-bottom: 56px;
  border-spacing: 0 !important;
}

.pricelist-table .tr-gray {
  background-color: #F7F7F7;
}

.pricelist-table .tr-white {
  background-color: #ffffff;
}

.pricelist-table .td-top {
  padding: 20px 25px;
  border: 1px solid #92CEE9;
}

.pricelist-table .td-top_big {
  width: 42%;
}

.pricelist-table .td-top_small {
  width: 14%;
}

.pricelist-table .td-small {
  padding: 20px 25px;
  text-align: center;
}

.pricelist-table td {
  padding: 20px 0 20px 25px;
}

.pricelist-table td .td-title {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  font-family: "Inter", sans-serif;
  color: #3C3C3C;
}

.pricelist-table td .td-text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  font-family: "Inter", sans-serif;
  color: #3C3C3C;
}

.pricelist-table td .td-marked {
  width: 100%;
  height: 16px;
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='16' viewBox='0 0 15 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.58208 16C5.09317 16 4.6301 15.768 4.33338 15.3679L0.317515 9.97201C0.193356 9.80537 0.102712 9.61548 0.050765 9.41319C-0.00118198 9.2109 -0.0134133 9.00019 0.0147702 8.79309C0.0429537 8.58599 0.110999 8.38657 0.215017 8.20623C0.319035 8.0259 0.456985 7.86818 0.620981 7.74209C0.784918 7.61547 0.971814 7.523 1.17096 7.46998C1.3701 7.41696 1.57758 7.40442 1.7815 7.43309C1.98542 7.46176 2.18177 7.53107 2.3593 7.63705C2.53682 7.74303 2.69204 7.88359 2.81605 8.05069L5.45845 11.5984L12.1021 0.749434C12.3224 0.391229 12.6735 0.136516 13.0782 0.0411668C13.483 -0.0541826 13.9084 0.0176168 14.2612 0.240816C14.9951 0.704859 15.2211 1.68781 14.7625 2.43531L6.91284 15.2479C6.77884 15.4676 6.59414 15.6508 6.37477 15.7814C6.1554 15.9121 5.90799 15.9863 5.65402 15.9977C5.62929 16 5.60681 16 5.58208 16Z' fill='%2336BCBB'/%3E%3C/svg%3E%0A");
  background-repeat: no-repeat;
  background-position: center;
}

.pricelist-btn {
  text-align: center;
}

.lab {
  padding-top: 83px;
  padding-bottom: 74px;
  background-color: #F7F7F7;
}

.lab-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.lab-descr {
  flex-basis: 50%;
  margin-right: 52px;
}

.lab-title {
  margin-bottom: 40px;
  font-weight: 800;
  font-size: 52px;
  line-height: 61px;
  font-family: "Raleway", sans-serif;
  color: #3C3C3C;
}

.lab-text {
  width: 460px;
  margin-bottom: 50px;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  font-family: "Inter", sans-serif;
  color: #3C3C3C;
}

.lab-text:last-child {
  margin-bottom: 0;
}

.lab-preview {
  flex-basis: 30%;
}

.process {
  padding-top: 60px;
  padding-bottom: 73px;
  background-color: #F7F7F7;
}

.process-container {
  width: 100%;
  max-width: 980px;
  margin: auto;
  padding-right: 15px;
  padding-left: 15px;
}

.process-title {
  margin-bottom: 20px;
}

.babysitters {
  padding: 37px 0 94px;
}

.babysitters-filter {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 0 -15px 54px;
}

.babysitters-filter__item {
  width: calc(100% / 4 - 30px);
  margin: 0 15px;
}

.babysitters-filter__label {
  display: inline-block;
  margin-bottom: 12px;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #3C3C3C;
}

.babysitters-filter__select {
  display: block;
  width: 100%;
  appearance: none;
  background: #FFFFFF;
  border: 1px solid #C4C4C4;
  border-radius: 10px;
  padding: 12px 42px 12px 22px;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  line-height: 22px;
  color: #6D6D6D !important;
  outline: none;
  transition: all 0.3s ease;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: calc(100% - 16px) 20px;
  background-image: url("data:image/svg+xml,%3Csvg width='19' height='10' viewBox='0 0 19 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M2 2L9.5 8L17 2' stroke='%236D6D6D' stroke-width='3' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}

.babysitters-filter__select:hover {
  border-color: #6D6D6D;
}

.babysitters-filter__select:focus {
  border-color: #36BCBB;
}

.babysitters-results {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 -16px -40px;
}

.babysitters-results .card-babysitters {
  width: calc(100% / 3 - 32px);
  margin: 0 16px 40px;
}

.info-section {
  padding: 67px 0 98px;
}

.info-section__wrap {
  display: flex;
  align-items: flex-start;
}

.info-section__title {
  width: 48.8%;
  margin-right: 30px;
}

.info-section__text {
  width: 48.8%;
  max-width: 508px;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  line-height: 22px;
}

.info-section__text p {
  margin-bottom: 23px;
}

.info-section__text p:last-child {
  margin-bottom: 0;
}

.info-section__subtitle {
  margin-bottom: 22px;
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 22px;
  line-height: 27px;
}

.safety-section {
  padding: 50px 0 70px;
}

.safety-section__title {
  margin-bottom: 22px;
  font-family: "Raleway", sans-serif;
  font-weight: bold;
  font-size: 32px;
  line-height: 38px;
}

.safety-section__subtitle {
  font-family: "Inter", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #6D6D6D;
}

.filter-tags {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 72px;
}

.filter-tag {
  background-color: #ffffff;
  border: 1px solid #6D6D6D;
  box-sizing: border-box;
  border-radius: 30px;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  text-decoration: none;
  padding: 10px 20px;
  margin-right: 19px;
  transition: all .3s ease;
  cursor: pointer;
}

.filter-tag__link {
  text-decoration: none;
  color: #6D6D6D;
}

.filter-tag_active {
  background-color: #0081C8;
  border: 1px solid #0081C8;
}

.filter-tag_active .filter-tag__link {
  color: #ffffff;
}

.footer {
  margin-top: auto;
  background-color: #3C3C3C;
}

.footer__top {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 50px;
  padding-bottom: 27px;
  border-bottom: 0.5px solid rgba(255, 255, 255, 0.5);
}

.footer__privacy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0 21px;
}

.footer__col {
  width: 25%;
  max-width: 254px;
}

.footer__nav ul li {
  display: block;
  margin-bottom: 11px;
}

.footer__nav ul li:last-child {
  margin-bottom: 0;
}

.footer__nav ul li a {
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 16px;
  color: #FFFFFF;
  text-decoration: none;
}

.footer__nav ul li a:hover {
  color: #36BCBB;
}

.footer__text {
  font-size: 14px;
}

.footer-contacts {
  min-height: 72px;
  margin-bottom: 23px;
}

.footer-contacts:last-child {
  margin-bottom: 0;
}

.footer-contacts__heading {
  margin-bottom: 10px;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #AAAAAA;
}

.footer-contacts__link {
  margin-right: 20px;
  font-family: "Inter", sans-serif;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF;
}

.footer-contacts__small {
  margin-top: 10px;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  color: #FFFFFF;
}

@media (max-width: 1199px) {
  h1,
  .page-title {
    font-size: 44px;
  }

  .hero__title {
    max-width: 784px;
  }
}

@media screen and (max-width: 1023px) {
  .btn {
    padding: 16px 18px;
    font-size: 14px;
  }

  .hero_home {
    padding: 100px 0;
  }
}

@media screen and (max-width: 991px) {
  .hero__btn-wrap {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .hero__btn-wrap .btn {
    width: 45%;
    margin-bottom: 20px;
    margin-right: 0 !important;
  }
}

@media (max-width: 767px) {
  h1,
  .page-title {
    font-size: 36px;
  }

  .section-title {
    font-size: 16px;
  }
}

@media screen and (max-width: 767px) {
  .hero_home {
    padding-bottom: 60px;
  }
}

@media (max-width: 575px) {
  h1,
  .page-title {
    font-size: 24px;
  }
}

@media screen and (max-width: 575px) {
  .hero__btn-wrap .btn {
    width: 80%;
  }
}